import React from "react";
import Breadcrumbs from "react-router-dynamic-breadcrumbs";
import "./breadcrumbs.scss";
import { withState } from "./../Context";

const BreadcrumbsComponent = withState(
  ({
    state: {
      translations: { t }
    }
  }) => {
    const routesList = {
      "/": t("navbarHome"),
      "/about-us": t("navbarAboutUs"),
      "/contact-us": t("navbarContactUs"),
      "/house-rules": t("navbarHouseRules"),
      "/terms-and-conditions": t("navbarTermsAndConditions")
    };

    return (
      <Breadcrumbs
        mappedRoutes={routesList}
        WrapperComponent={(props) => (
          <ol className="breadcrumb">{props.children}</ol>
        )}
        ActiveLinkComponent={(props) => (
          <li className="breadcrumb-item active">{props.children}</li>
        )}
        LinkComponent={(props) => (
          <li className="breadcrumb-item">{props.children}</li>
        )}
      />
    );
  }
);

export default BreadcrumbsComponent;
